.list {
    list-style: none;
}


.garden {
    margin: 6% 12%;
    flex-grow:1
}


#rose {
    width: 30%;
    backface-visibility: hidden;
    transition-duration: 0.3s;
    transition-property: transform;
}

#rose:hover {
    transform: scale(1.1);
}


.parent{
    display:flex;
}